.article-container {
    width: 100%;
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(4, 1fr);
}

.article-image {
    max-width: 100%;    
    margin-bottom: 1rem;
}

.card:hover {
    cursor: pointer;
}

.card-title {
    margin: 0;
    font-weight: 300;
}